.main-nav__list {
  display: none;
}
.display_menu {
  position: absolute;
  z-index: 10;
  left: 0;
  padding: 0;
  margin: 0;
  list-style: none;
  background: #81b3d2;
  width: 320px;
  text-align: center;
  display: flex;
  flex-direction: column;
  min-height: 223px;
  top: 0;
}
.display_menu .main-nav__item {
  padding-top: 17px;
  padding-bottom: 15px;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2);
}
.display_menu .main-nav__item .main-nav__link {
  line-height: 26px;
  font-size: 18px;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
}
.display_menu .main-nav__item--shadow {
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
.display_menu .main-nav__cross {
  background: none;
  border: none;
  padding: 0;
  display: block;
  position: absolute;
  right: 20px;
  top: 17px;
  cursor: pointer;
}
.main-nav .main-nav__toggle {
  background: none;
  border: none;
  cursor: pointer;
  margin: 0;
  padding: 0;
}
.main-nav .main-nav__toggle::before {
  content: url("../img/icon-menu.svg");
  display: block;
  width: 23px;
  height: 22px;
}
.main-nav .main-nav__item--invisible {
  display: none;
}
.main-nav .main-nav__item:hover .main-nav__link {
  color: #766357;
}
@media (min-width: 768px) {
  .main-nav .main-nav__list {
    position: relative;
    visibility: visible;
    width: 768px;
    display: flex;
    align-items: center;
    margin: 0 auto;
    text-align: center;
    list-style: none;
    padding: 0;
    margin: 0;
  }
  .main-nav .main-nav__item {
    width: 153px;
  }
  .main-nav .main-nav__item--second {
    margin-right: 180px;
  }
  .main-nav .main-nav__toggle {
    display: none;
  }
  .main-nav .main-nav__link {
    display: block;
    padding-top: 18px;
    padding-bottom: 17px;
    color: #000000;
    text-decoration: none;
    text-transform: uppercase;
  }
  .main-nav .main-nav__item--invisible {
    display: block;
    position: absolute;
    left: 40%;
    z-index: 3;
    padding-top: 27px;
  }
  .main-nav .main-nav__cross {
    display: none;
  }
}
@media (min-width: 1280px) {
  .main-nav .main-nav__list {
    position: relative;
    visibility: visible;
    width: 1200px;
    display: flex;
    align-items: center;
    margin: 0 auto;
    text-align: center;
    list-style: none;
    padding: 0;
    margin: 0;
  }
  .main-nav .main-nav__item {
    width: 240px;
  }
  .main-nav .main-nav__item--second {
    margin-right: 340px;
  }
  .main-nav .main-nav__toggle {
    display: none;
  }
  .main-nav .main-nav__link {
    display: block;
    padding-top: 18px;
    padding-bottom: 17px;
    color: #000000;
    text-decoration: none;
    text-transform: uppercase;
  }
  .main-nav .main-nav__item--invisible {
    display: block;
    position: absolute;
    left: 40%;
    z-index: 3;
    padding-top: 27px;
  }
  .main-nav .main-nav__cross {
    display: none;
  }
}
